import { ActivatedRoute } from '@angular/router';
import { ApiService } from '../api.service';
import { Component, OnInit } from '@angular/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-post',
  templateUrl: './post.component.html',
  styleUrls: ['./post.component.css']
})
export class PostComponent implements OnInit {
  postId: number;
  post: any;
  postImageUrls:{ [postId: number]: SafeResourceUrl } = {};
  isUserValid: any;
  users: any;
  imageUrl: SafeResourceUrl | null = null; 
  header: string | null = null;
  userId: any;

  constructor(
    private route: ActivatedRoute,
    private apiService: ApiService,
    private sanitizer: DomSanitizer 
  ) {}


  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.postId = +params['id'];
      this.fetchPostDetails();
      this.fetchCommentsForPost(); 
    });
  }

  fetchPostDetails() {
    this.apiService.getPosts().subscribe(
      (posts) => {
        this.post = posts.find((post: any) => post.id === this.postId);
        if (this.post) {
          const imageUrl = this.post.url; 
          this.imageUrl = this.sanitizer.bypassSecurityTrustResourceUrl(imageUrl);
        }
      },
      (error) => {
        console.error('Failed to fetch post details:', error);
      }
    );
  }

  fetchCommentsForPost() {
    if (this.postId) { 
      this.apiService.getComments().subscribe(
        (comments) => {
          console.log('All comments:', comments); 
          this.post.comments = comments.filter((comment: any) => comment.postId === this.postId);
          console.log('Filtered comments:', this.post.comments); 
        },
        (error) => {
          console.error('Failed to fetch comments:', error);
        }
      );
    }
  }
}
