import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-journey-report',
  templateUrl: './journey-report.component.html',
  styleUrls: ['./journey-report.component.css']
})
export class JourneyReportComponent implements OnInit {
  userId: number;
  file: File;
  content: string = '';
  header: string = '';
  selectedFile: File | null = null;
  isUserValid: boolean = true; 

  constructor(private apiService: ApiService, private router: Router) { }

  ngOnInit(): void {
    const userItem = localStorage.getItem('user');
    if (userItem) {
      this.userId = JSON.parse(userItem).id;
    } else {
      this.isUserValid = false; 
    }
  }

  createPost(): void {
    if (!this.isUserValid) {
      console.log('Invalid user');
      
      return;
    }

    let obj = {
      'content': this.content,
      'header': this.header,
      'file': this.file.name,
      'userId': this.userId
    }

    this.apiService.createPost(obj).subscribe(
      (response) => {
        console.log('Post created successfully', response);
        alert('Post created successfully');
        this.router.navigate(['/posts']);
      },
      (error) => {
        console.error('Error creating post', error);
      }
    );
  }

  onFileSelected(event: Event): void {
    const inputElement = event.target as HTMLInputElement;
    if (inputElement.files && inputElement.files.length > 0) {
      this.file = inputElement.files[0];
      console.log('53535', this.file);
    }
  }
}
