import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './login/login.component';
import { RegisterComponent } from './register/register.component';
import { HomeComponent } from './home/home.component';
import { AdminComponent } from './admin/admin.component';
import { BasicComponent } from './basic/basic.component';
import { SupervisiorComponent } from './supervisior/supervisior.component';
import { UnauthorisedComponent } from './unauthorised/unauthorised.component';
import { CreatecommentComponent } from './createcomment/createcomment.component';
import { JourneyReportComponent } from './journey-report/journey-report.component';
import { PublicPageComponent } from './public-page/public-page.component';

import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';

import { HeaderDetailComponent } from './header-detail/header-detail.component';
import { PostComponent } from './post/post.component';


const routes: Routes = [
  { path: '', redirectTo: '/posts', pathMatch: 'full' },
  { path: 'login', component: LoginComponent },
  { path: 'register', component: RegisterComponent },
  { path: 'header', component: HeaderDetailComponent},
  
  { path: 'admin', component: AdminComponent },
  { path: 'basic', component: BasicComponent },
  { path: 'supervisior', component: SupervisiorComponent},
  { path: 'unauthorisied', component: UnauthorisedComponent},
  { path: 'comments/:postId', component: CreatecommentComponent},
  {path: 'report', component:JourneyReportComponent},
  {path: 'posts', component:PublicPageComponent},
  {path: 'forgot-password', component:ForgotPasswordComponent},
  
  {path:'post/:id',component:PostComponent}
  // { path: 'reset-password', component: ResetPasswordComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }

