import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {
  roles: string;
  users: any[];
  showPasswordResetForm: boolean = false;
  oldPassword: string;
  newPassword: string;
  email:string;
  user: unknown;

  constructor(private router: Router, private apiService: ApiService) { }

  ngOnInit(): void {
    this.roles = localStorage.getItem('role'); 
  
    if (!this.roles) {
      this.router.navigate(['/login']);
      return; 
    }
  
    if (this.roles !== 'admin') {
      alert('Checking credentials admin');
      this.router.navigate(['/unauthorisied']);
      return; 
    }
  
    this.apiService.getusers().subscribe(
      (response) => {
        this.users = response;
      },
      (error) => {
        console.error(error);
      }
    );

    this.user = JSON.parse(localStorage.getItem('user'));
    this.email = this.user['email'];
  }
  

  performDelete(id: number): void {
    this.apiService.deleteUser(id).subscribe(
      () => {
        console.log('User deleted successfully');
      },
      (error) => {
        console.error('Failed to delete user:', error);
      }
    );
  }

  performUpdate(id: number): void {
    const user = this.users.find(u => u.id === id);
    if (!user) return;
  
    user.isEditing = true; // Enable editing mode
  
    const userData = {
      name: user.name,
      email: user.email,
      password: user.password,
      roles: user.roles
    };
  
    this.apiService.updateUser(id, userData).subscribe(
      (response) => {
        console.log('User updated successfully');
        
      },
      (error) => {
        console.error('Failed to update user:', error);
     
      }
    );
  }
  
  cancelUpdate(user: any): void {
    user.isEditing = false; // Disable editing mode
  }

  onResetPassword() {
    this.showPasswordResetForm = true;
  }

  cancelResetPassword() {
    this.showPasswordResetForm = false;
    this.oldPassword = '';
    this.newPassword = '';
  }

  onResetPasswordSubmit() {
    this.apiService. resetP(this.email,this.oldPassword, this.newPassword).subscribe(
      () => {
       
        this.showAlert('success', 'Password reset successfully');
     
        this.cancelResetPassword();
      },
      (error) => {
        console.error(error);
        this.showAlert('error', 'Failed to reset password');
      }
    );
  }

  showAlert(type: string, message: string) {
    if (type === 'success') {
      alert(message);
    } else if (type === 'error') {
      alert(message);
    } else {
      console.warn('Unsupported alert type:', type);
    }
  }

  logout(): void {
    this.apiService.logout().subscribe(
      (response) => {
        // Clear the token, role, and user from local storage
        localStorage.removeItem('token');
        localStorage.removeItem('role');
        localStorage.removeItem('user');
        console.log('Logged out successfully');
  
        // Get the expiration time from the response
        const expirationTime = response.expirationTime;
  
        // Use the expiration time if needed
        console.log(`Token expired at: ${new Date(expirationTime)}`);
  
        sessionStorage.clear();
        this.router.navigate(['/posts']);
      },
      (error) => {
        console.error('Logout failed:', error);
      }
    );
  }
}
