import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'app-createcomment',
  templateUrl: './createcomment.component.html',
  styleUrls: ['./createcomment.component.css']
})
export class CreatecommentComponent implements OnInit {
  content: string;
  postid: any;
  userId: any;
  user: any;

  constructor(
    private apiService: ApiService,
    private route: ActivatedRoute,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.route.params.subscribe(params => {
      this.postid = params['postid'];
    });

    const userItem = localStorage.getItem('user');
    if (userItem) {
      this.userId = JSON.parse(userItem).id;
    }
  }

  submitComment() {
    if (this.content) {
      this.apiService.createComment(this.content, this.postid).subscribe(
        response => {
          console.log('Response:', response);
          alert('Comment saved successfully');
          this.router.navigate(['/posts']);
        },
        error => {
          console.error('Error:', error);
        }
      );
    } else {
      console.log('Content is required.');
    }
  }
}
