import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ApiService } from '../api.service';

@Component({
  selector: 'app-basic',
  templateUrl: './basic.component.html',
  styleUrls: ['./basic.component.css']
})
export class BasicComponent implements OnInit {
  roles: string;
  showPasswordResetForm: boolean = false;
  oldPassword: string;
  newPassword: string;
  email:any;
  user: unknown;


  constructor(private router: Router, private apiService: ApiService) { }

  ngOnInit(): void {
    this.roles =localStorage.getItem('role'); 
    if(!this.roles){
      this.router.navigate(['/login']);
    }
    this.user = JSON.parse(localStorage.getItem('user'));
    this.email = this.user['email'];

  }

  onResetPassword() {
    this.showPasswordResetForm = true;
  }

  cancelResetPassword() {
    this.showPasswordResetForm = false;
    this.oldPassword = '';
    this.newPassword = '';
  }

  onResetPasswordSubmit() {
    this.apiService. resetP(this.email,this.oldPassword, this.newPassword).subscribe(
      () => {
      
        this.showAlert('success', 'Password reset successfully');
      
        this.cancelResetPassword();
      },
      (error) => {
        console.error(error);
        this.showAlert('error', 'Failed to reset password');
      }
    );

    
  }
  showAlert(type: string, message: string) {
    if (type === 'success') {
      alert(message);
    } else if (type === 'error') {
      alert(message);
    } else {
      console.warn('Unsupported alert type:', type);
    }
  }
  logout(): void {
    this.apiService.logout().subscribe(
      (response) => {
    
        localStorage.removeItem('token');
        localStorage.removeItem('role');
        localStorage.removeItem('user');
        console.log('Logged out successfully');
  
    
        const expirationTime = response.expirationTime;
  
        this.router.navigate(['/posts']);
      },
      (error) => {
        console.error('Logout failed:', error);
      }
    );
  }
}
