import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-forgot-password',
  templateUrl: './forgot-password.component.html',
  styleUrls: ['./forgot-password.component.css']
})
export class ForgotPasswordComponent implements OnInit {
  email: any;
  otp: any;
  newPassword: any;
  

  constructor(private apiService: ApiService, private router: Router,) { }

  ngOnInit(): void {
  }

  sendOTP() {
    document.getElementById('main_loader').style.display = "block";
    
    if (!this.email) {
      alert('Please provide a valid email ID.');
      document.getElementById('main_loader').style.display = "none"; // Stop the spinner
      return;
    }
  
    this.apiService.sendOTPByEmail(this.email).subscribe(
      (response) => {
        console.log('OTP sent successfully:', response);
  
        document.getElementById('main_loader').style.display = "none"; // Stop the spinner
        document.getElementById('showMessage').style.display = "block";
  
        setTimeout(() => {
          document.getElementById('showMessage').style.display = "none";
        }, 3000);
  
        this.otp = this.otp;
      },
      (error) => {
        console.error('Failed to send OTP:', error);
        document.getElementById('main_loader').style.display = "none"; // Stop the spinner
      }
    );
  }
  

  resetPassword() {

    if (!this.email || !this.otp || !this.newPassword) {
      alert('Please fill all the required fields');
      return;
    }
    this.apiService.resetPassword(this.email, this.otp, this.newPassword).subscribe(
      (response) => {
        console.log('Password reset successful:', response);
        alert('Password reset successful'); 
        this.email = '';
        this.otp = ''; 
        this.newPassword = ''; 
      },
      (error) => {
        console.error('Failed to reset password:', error);
      }
    );
  } 
}
